export class InAppDialogEvents {
    campaignId: string
    campaignName: string
    cohortId: string
    startTime: string
    endTime: string

    constructor(campaignId: string, campaignName: string, cohortId: string, startTime: string, endTime: string) {
        this.campaignId = campaignId
        this.campaignName = campaignName
        this.cohortId = cohortId
        this.startTime = startTime
        this.endTime = endTime
    }
}
